* {
	padding: 0;
	margin: 0;
	border: 0
}

*,
:after,
:before {
	box-sizing: border-box
}

:active,
:focus {
	outline: none
}

a:active,
a:focus {
	outline: none;
	letter-spacing: 1.2px
}

aside,
footer,
header,
nav {
	display: block
}

body,
html {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%
}

body,
button,
html,
input,
textarea {
	font-family: inherit
}

input::-ms-clear {
	display: none
}

button {
	cursor: pointer
}

button::-moz-focus-inner {
	padding: 0;
	border: 0
}

a,
a:visited {
	text-decoration: none!important
}

a:hover {
	text-decoration: none
}

ul li {
	list-style: none
}

img {
	vertical-align: top
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: inherit
}

.fas {
	margin-right: 5px
}

.text-center {
	margin: 20px 0;
	font-size: 24px;
	font-weight: bold
}

.mb-style {
	width: 500px
}

.mb-wrap {
	width: 300px;
	margin: 20px auto;
	padding: 20px
}

.mb-wrap,
.mb-wrap blockquote {
	position: relative
}

.mb-style blockquote {
	background: #fff;
	padding: 30px;
	border-radius: 5px;
	box-shadow: inset 0 3px 0 gold, -5px -4px 25px rgba(0, 0, 0, .3)
}

.mb-style blockquote:after,
.mb-style blockquote:before {
	top: 100%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none
}

.mb-style blockquote:after {
	border-top-color: #fff;
	border-width: 10px;
	left: 65%;
	margin-left: -10px
}

.mb-style blockquote:before {
	border-top-color: rgba(0, 0, 0, .01);
	border-width: 11px;
	left: 65%;
	margin-left: -11px
}

.mb-style blockquote p {
	font-family: "Alegreya", serif;
	font-size: 24px;
	color: #b4b4b4;
	font-weight: 400;
	line-height: 40px;
	font-style: italic;
	text-indent: 100px;
	position: relative
}

.mb-style blockquote p:before {
	content: "\201C";
	font-family: serif;
	font-style: normal;
	font-weight: 700;
	position: absolute;
	font-size: 175px;
	top: 0;
	left: -105px;
	color: #837d7d;
	text-shadow: 7px 14px 10px rgba(0, 0, 0, .1)
}

.mb-style .mb-attribution {
	text-align: right;
	padding: 20px 100px 20px 20px;
	position: relative
}

.Buffett,
.Franklin,
.Gates {
	display: block;
	width: 80px;
	height: 80px;
	border: 5px solid #e9e6e6;
	border-radius: 50%;
	position: absolute;
	object-fit: cover;
	right: 10px;
	bottom: 5px;
	box-shadow: inset 1px 1px 4px rgba(0, 0, 0, .5), 0 2px 3px rgba(0, 0, 0, .6)
}

.mb-style .mb-author {
	font-family: "Alegreya SC", serif;
	font-weight: 700;
	font-size: 18px;
	letter-spacing: 1px;
	color: #dfdb13;
	text-shadow: 0 1px 1px hsla(0, 0%, 100%, .7)
}

.img-logo {
	display: block;
	width: 65px;
	height: 65px;
	margin: 20px 0;
	opacity: 1;
	z-index: 99;
	cursor: pointer
}

.links {
	margin: 0 20px;
	color: gold;
	font-weight: 700;
	font-size: 20px;
	letter-spacing: 1.2px;
	display: flex;
	opacity: .8;
	transition: .4s linear .1s;
	z-index: 10;
	text-transform: uppercase
}

.links-logo {
	margin: 0 20px
}

.links:hover {
	color: gold;
	opacity: 1
}

.navbar-wrapper {
	min-height: 50px
}

.nav-common {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	text-align: center;
	position: relative;
	left: 80px
}

.nav-links {
	display: flex;
	margin: 0 22px;
	letter-spacing: 1.2px;
	font-size: 20px;
	color: #17a2b8;
	transition: .4s linear .1s;
	text-transform: uppercase
}

.nav-links:hover {
	color: wheat
}

.btn-active {
	box-shadow: 0 4px 0 #0c7080;
	position: relative;
	top: 0
}

.forgot-password {
	color: #a6acb9;
	transition: all .4s linear .1s;
	font-size: 18px
}

.forgot-password:hover {
	color: #fff
}

.btn-active:active {
	top: 3px;
	box-shadow: 0 2px 0 #3d5558
}

.btn-log-Out {
	position: absolute;
	top: 15px;
	right: 20px;
	font-size: 20px;
	padding: 5px
}

.btn-log-Out:active {
	top: 18px
}

.NewsGrid {
	margin: 10px;
	border: 4px solid #d3d3d3;
	border-radius: 10px;
	padding: 20px
}

.pagination-news {
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	padding: 10px 0;
	font-size: 20px
}

.news-title {
	font-size: 24px
}

.news-title-artical {
	letter-spacing: .8px;
	font-weight: 700;
	font-size: 20px
}

.col-md-9 {
	position: relative;
	left: 20px
}

.news-content {
	margin: 10px 0;
	color: #252121;
	font-size: 17px
}

.news-description {
	font-size: 17px
}

.form__signin {
	margin: 80px 0 0
}

.overlay {
	background: #000;
	opacity: .75;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: -1
}

.city {
	outline: 0;
	border: none
}

.city,
.wrap {
	background: #c0bdbd
}

.wrapper-flex {
	display: flex;
	justify-content: space-around;
	padding: 30px 0
}

.time {
	display: flex;
	flex-direction: column;
	align-items: center
}

.today {
	text-transform: uppercase;
	letter-spacing: .6px;
	font-size: 20px;
	font-weight: 700;
	color: #c0923c
}

.clock {
	width: 200px;
	height: 80px;
	border: 5px solid grey;
	border-radius: 5px;
	color: #fff;
	background-color: #109db3;
	text-align: center;
	font-size: 45px
}

.weather {
	display: flex;
	flex-direction: column;
	align-items: center
}

.weather-city {
	color: #1a5c64
}

.weather-city,
.weather-description {
	text-transform: uppercase;
	letter-spacing: .6px;
	margin: 0 0 5px;
	font-size: 20px
}

.weather-description {
	color: #539aa3
}

.exchange {
	display: flex;
	flex-direction: column;
	align-items: center
}

.exchange-title {
	text-transform: uppercase;
	letter-spacing: .8px;
	color: #c0923c;
	font-weight: 700;
	font-size: 20px
}

.exchange-cur {
	text-transform: uppercase;
	letter-spacing: .6px;
	font-size: 18px;
	color: #278364;
	margin: 0 0 5px 0
}

.exchange-cur span {
	color: #000
}

.balance-tab,
.expenses-chart {
	margin: 20px 0 0
}

.balance-tab {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border-radius: 8px;
	position: absolute;
	left: 40px
}

.balance-tab h3 {
	padding: 0 0 5px;
	font-size: 24px;
	font-weight: bold
}

.balance-tabs {
	font-size: 20px;
	text-transform: uppercase;
	box-shadow: 0 0 10px rgba(20, 18, 18, .8);
	border-radius: 8px
}

.balance-total {
	content: "+";
	font-size: 25px;
	margin-top: 10px;
	color: green
}

.balance-total-red {
	content: "-";
	font-size: 25px;
	margin-top: 10px;
	color: red
}

.balance-income {
	font-size: 25px;
	margin-top: 10px;
	color: green
}

.balance-expenses {
	font-size: 25px;
	margin-top: 10px;
	color: red
}

.make-visible {
	display: flex
}

.btn-visible {
	margin: 0 0 0 20px
}

.form-data {
	margin: 0 10px 0 0;
	width: 150px
}

.wrapper-balance-sort {
	display: flex;
	justify-content: flex-end;
	position: relative
}

.accordion {
	margin: 30px 40px 0 0;
	border: 2px solid gold;
	border-radius: 8px;
	box-shadow: 0 0 10px rgba(65, 61, 61, .8);
	display: flex;
	flex-direction: column
}

.img-about{
	display: flex;
	max-width: 90%;
	height: 400px;
	margin: 10px auto;
}

.wrapper-about-app{
	margin: 0;
	padding: 10px 15px ; 
	background:rgba(0, 0, 0, 0.7);
}

.wrapper-about-app p {
	color:rgb(194, 182, 144);
}
.wrapper-about-app li {
	color:rgb(194, 182, 144);
}

.about-app{
	text-transform: uppercase;
	color:rgb(199, 157, 18);
	margin: 20px 0px ;
}

.nav{
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: 0px 0px 0px 100px;
} 
.balance-tabs{
	margin: 0px 0px 0px 0px;
}
.wrappers {
	padding: 0 10px;
	margin: 0 auto;
}

.select-feild {
	width: 160px
}

.footer{
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: 0 auto;
}

.footer div{
	margin: 0px 0px 0px -50px;
}

.github-icon{
	max-width: 40px;
	margin: 20px 0px 20px 0px;
}

.link-rs{
	margin: 0px 10px 0px 0px;
	font-size: 18px;
	color:#17a2b8;
	transition: all .4s linear .1s;
}

.link-rs:hover{
	color:wheat;
}

.footer__link {
	margin: -2px 0px 0px 10px;
	font-size: 18px;
	color:#17a2b8;
	transition: all .4s linear .1s;
}

.footer__link:hover{
	color:wheat;
}

.form-label {
	margin: 10px 0;
	font-size: 17px
}

.accordion-toogle {
	color: #1eaf84;
	cursor: pointer;
	text-transform: uppercase;
	font-weight: 700;
	letter-spacing: .6px
}

.charts {
	background: #f0ffdf;
	padding: 0 0 60px
}

.crart-size {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 auto
}

thead {
	font-size: 17px
}

th,
td {
	font-size: 17px
}

tbody,
thead {
	text-align: center
}

tbody {
	font-size: 15px
}

.form-settings {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 20px auto auto
}

.kaQZNd,
.sc-gtssRu {
	height: 97%!important
}

.img-about-data{
	margin: 10px 0px 10px 0px;
	max-width: 100%;
}

@media (max-width:1200px) {
	.NewsGrid {
		align-items: center;
		text-align: center;
		margin: 0 0 15px
	}
	.col-md-3,
	.NewsGrid {
		display: flex;
		justify-content: center
	}
	.col-md-3 {
		margin: 0 0 20px 1px
	}
	.nav-links {
		margin: 0 12px
	}
	.links-logo {
		margin: 0 14px
	}
}

@media (max-width:900px) {
	.nav-links {
		margin: 0 10px;
		font-size: 17.7px
	}
	.links-logo {
		margin: 0 12px
	}
}

@media (max-width:767px) { 
	.nav-common,
	.navbar-wrapper {
		position: relative
	}
	.nav-common {
		display: flex;
		flex-direction: column;
		left: 0
	}
	.nav-links {
		margin: 10px 15px;
		font-size: 18px
	}
	.form__signin {
		margin: 200px 0 40px
	}
	.wrapper-flex {
		display: flex;
		flex-direction: column;
		padding: 25px 0
	}
	.clock,
	.weather {
		margin: 0 0 20px
	}
	.make-visible {
		display: flex;
		flex-direction: column
	}
	.btn-visible {
		margin: 20px 0 0
	}
	.wrapper-balance-sort {
		display: flex;
		flex-direction: column;
		position: relative
	}
	.balance-tab {
		position: static;
		left: 0
	}
	.accordion {
		justify-content: center;
		margin: 20px auto
	}
	td,
	th {
		padding: 8px!important
	}
	.crart-bar {
		height: 400px!important
	}
	.btn-log-Out {
		position: static
	}
	.crart-size {
		display: flex;
		justify-content: center;
		margin: 0 auto;
		height: 500px!important
	}
	.links {
		margin: 0 10px
	}
}

@media (max-width:500px) {
	.hidden {
		display: none
	}
	td,
	th {
		padding: 5px!important
	}
	.crart-bar {
		height: 350px!important
	}
	.balance-tab h3 {
		font-size: 21px
	}
	.Toastify__toast--success {
		width: 320px;
		margin: 50px auto
	}
	.Toastify__toast--error {
		margin: 50px auto;
		width: 320px
	}
}

@media (max-width:450px) {
	.nav{
		margin: 0px 0px 0px 20px;
	}
	td,
	th {
		padding: 3px!important
	}
	.crart-size {
		display: flex;
		justify-content: center;
		margin: 0 auto;
		height: 450px!important
	}
	.balance-tabs {
		font-size: 17px
	}
	.nav-item {
		padding: 6px 10px!important
	}
}

@media (min-width:768px) {
	.crart-bar {
		margin: 0 auto;
		width: 750px!important;
		height: 330px!important
	}
	.crart-size {
		display: flex;
		justify-content: center;
		margin: 0 auto;
		height: 550px!important
	}
}